export default {
  props: {
    shadowColor: {
      type: [String, null],
      default: null
    },

    shadowXOffset: {
      type: Number,
      default: 0
    },

    shadowYOffset: {
      type: Number,
      default: 0
    },

    shadowBlur: {
      type: Number,
      default: 0
    }
  },

  computed: {
    shadow() {
      if (this.shadowColor) {
        return {
          color: this.shadowColor,
          blur: this.shadowBlur,
          offsetX: this.shadowXOffset,
          offsetY: this.shadowYOffset
        }
      }

      return null;
    }
  },

  watch: {
    shadow: {
      handler(newShadow) {
        this.promiseFabricCanvas().then(canvas => {
          if (this.instance) {
            this.instance.setShadow(newShadow);
            canvas.renderAll();
          }
        });
      },
      immediate: true
    }
  }
};