import {createPropAndDataWatchers} from '../../../../util';

export default {
  props: {
    shadowColor: {
      type: [String, null],
      default: null
    },
    shadowXOffset: Number,
    shadowYOffset: Number,
    shadowBlur: Number
  },

  data() {
    return {
      shadow: false,
      localShadowColor: '#000',
      localShadowXOffset: null,
      localShadowYOffset: null,
      localShadowBlur: null
    };
  },

  watch: {
    ...createPropAndDataWatchers([
      'shadowXOffset', 'shadowYOffset', 'shadowBlur'
    ]),

    shadowColor: {
      handler(newColor) {
        if (newColor) {
          if (newColor !== this.localShadowColor) {
            this.localShadowColor = newColor;
          }
          this.shadow = true;
        } else if (!newColor && this.shadow) {
          this.shadow = false;
        }
      },
      immediate: true
    },

    localShadowColor(newColor) {
      if (newColor && newColor !== this.shadowColor && this.shadow) {
        this.$emit('update:shadow-color', newColor);
      }
    },

    shadow(newVal) {
      if (!newVal && this.shadowColor) {
        this.$emit('update:shadow-color', null);
      } else if (newVal && (!this.shadowColor || this.shadowColor !== this.localShadowColor)) {
        this.$emit('update:shadow-color', this.localShadowColor);
      }
    }
  }
};