<template>
    <VSheet tile color="blue-grey lighten-2" class="pa-1">
        <VRow dense>
            <VCol cols="6">
                <VCard>
                    <FileSelectionArea @input="$emit('file-selected', $event)" :enable-drop-zone="enableDropZone">
                        <template>
                            <VResponsive
                                    :aspect-ratio="1"
                                    class="d-flex align-center justify-center blue-grey lighten-1"
                            >
                                <div class="text-center white--text">
                                    <VIcon size="48" color="white">mdi-plus-thick</VIcon>
                                    <br>
                                    
                                </div>
                            </VResponsive>
                        </template>
                    </FileSelectionArea>
                </VCard>
            </VCol>


            <template v-if="gallery.length">
                <VCol cols="6" v-for="item in gallery" :key="item.name">
                    <VCard @click="$emit('url-selected', item.url)" dark>
                        <VImg
                                :src="item.preview" :aspect-ratio="1"
                                gradient="to bottom, rgba(0,0,0,.8), transparent 50%"
                        >
                            <VCardActions>{{ item.name }}</VCardActions>
                        </VImg>
                    </VCard>
                </VCol>
            </template>

            <VCol cols="6" v-if="loadingGallery" class="d-flex align-center justify-center">
                <VProgressCircular indeterminate/>
            </VCol>
        </VRow>
    </VSheet>
</template>

<script>
  import FileSelectionArea from '../../file-selection-area';

  import cityUrl from '../../../assets/backgrounds/city.jpg';

  import fieldUrl from '../../../assets/backgrounds/field.jpg';

  const forestUrl = require('../../../assets/backgrounds/forest.jpg');
  const planetUrl = require('../../../assets/backgrounds/planet.jpg');
  const shuttleUrl = require('../../../assets/backgrounds/shuttle.jpg');
  const starportUrl = require('../../../assets/backgrounds/starport.jpg');
  const startrailUrl = require('../../../assets/backgrounds/startrail.jpg');
  const fakeGallery = [{
    name: 'City',
    preview: cityUrl,
    url: cityUrl
  }, {
    name: 'Field',
    preview: fieldUrl,
    url: fieldUrl
  }, {
    name: 'Forest',
    preview: forestUrl,
    url: forestUrl
  }, {
    name: 'Planet',
    preview: planetUrl,
    url: planetUrl
  }, {
    name: 'Shuttle',
    preview: shuttleUrl,
    url: shuttleUrl
  }, {
    name: 'Starport',
    preview: starportUrl,
    url: starportUrl
  }, {
    name: 'Star Trail',
    preview: startrailUrl,
    url: startrailUrl
  }];

  function loadGallery() {
    return new Promise(resolve => {
      setTimeout(() => resolve(fakeGallery), 1000);
    });
  }

  export default {
    name: 'background-properties',

    components: {
      FileSelectionArea
    },

    props: {
      enableDropZone: {
        type: Boolean,
        default: false
      }
    },

    data() {
      return {
        loadingGallery: false,
        gallery: []
      }
    },

    mounted() {
      this.loadGallery();
    },

    methods: {
      loadGallery() {
        this.loadingGallery = true;
        loadGallery().then(gallery => this.gallery = gallery)
          .finally(() => this.loadingGallery = false);
      }
    }
  }
</script>