<script>
  import {fabric} from 'fabric';
  import VFabricStaticCanvas from './v-fabric-static-canvas';

  export default {
    name: 'v-fabric-canvas',

    extends: VFabricStaticCanvas,

    methods: {
      buildCanvasObject() {
        const canvas = new fabric.Canvas(this.$refs.canvas, this.buildCanvasOptions());

        if (this.zoom) {
          canvas.setZoom(this.zoom);
        }

        //canvas.preserveObjectStacking = true;
        canvas.selection = false;

        return canvas;
      }
    }
  }
</script>