<template>
    <VMenu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :close-on-click="true"
            :return-value.sync="color"
            transition="scale-transition"
            offset-y
            min-width="290px"
            :disabled="disabled"
    >
        <template v-slot:activator="{ on: menuOn }">
            <VTooltip top>
                <template v-slot:activator="{on: tooltipOn}">
                    <VBtn v-on="{...menuOn, ...tooltipOn}">
                        <VRow
                                align="center"
                                class="flex-column"
                                justify="center"
                        >
                            <VIcon class="cols 12">{{ icon }}</VIcon>

                            <VSheet
                                    tile
                                    style="margin-top: -4px;"
                                    height="4"
                                    width="26"
                                    :color="color"
                            />
                        </VRow>
                    </VBtn>
                </template>
                {{ tooltip }}
            </VTooltip>
        </template>

        <VCard>
            <VColorPicker
                    v-model="color"
                    :hide-mode-switch="true"
                    mode="rgba"
            />

            <VCardActions>
                <VBtn small @click="$refs.menu.save(color)" color="primary">
                    Ok
                </VBtn>

                <VBtn small @click="menu = false">
                    
                </VBtn>

                <slot name="actions" :close-menu="closeMenu" />
            </VCardActions>
        </VCard>
    </VMenu>
</template>

<script>
  export default {
    name: 'color-button',

    props: {
      value: {
        type: String,
        default: '#000'
      },

      label: String,

      icon: String,

      disabled: Boolean,

      tooltip: String
    },

    data() {
      return {
        menu: false,
        color: this.value
      };
    },

    computed: {
      sheetColor() {
        return this.color;
      }
    },

    watch: {
      value: {
        handler(newColor, oldColor) {
          if (newColor !== oldColor) {
            this.color = newColor;
          }
        },
        immediate: true
      },

      color(newColor, oldColor) {
        if (newColor !== oldColor) {
          this.$emit('input', newColor);
        }
      }
    },

    methods: {
      closeMenu() {
        this.menu = false;
      }
    }
  }
</script>