<template>
    <VCard class="pa-1 ma-1">
        <VCardTitle>
            <span> </span>
            <VSpacer/>
            <VBtn icon color="red" dark @click="$emit('delete')">
                <VIcon>mdi-delete-forever</VIcon>
            </VBtn>
        </VCardTitle>

        <VCardText>
            <VTextarea
                    v-model="localText"
                    label=""
                    rows="2"
            />

            <VRow dense>
                <VCol cols="9">
                    <VSelect
                            label=""
                            v-model="localFontFamily"
                            :items="fonts"
                    >
                        <template v-slot:item="{ item }">
                        <span :style="{ fontFamily: item.value }">
                            {{ item.text }}
                        </span>
                        </template>
                    </VSelect>
                </VCol>

                <VCol cols="3">
                    <VTextField
                            v-model.number="localFontSize"
                            label=""
                            type="number"
                            min="1"
                            max="500"
                            step="1"
                    />
                </VCol>
            </VRow>

            <VBtnToggle multiple v-model="format" class="mr-1 mb-2">
                <VTooltip top>
                    <template v-slot:activator="{on}">
                        <VBtn v-on="on">
                            <VIcon>mdi-format-bold</VIcon>
                        </VBtn>
                    </template>
                    
                </VTooltip>

                <VTooltip top>
                    <template v-slot:activator="{on}">
                        <VBtn v-on="on">
                            <VIcon>mdi-format-italic</VIcon>
                        </VBtn>
                    </template>
                    
                </VTooltip>

                <VTooltip top>
                    <template v-slot:activator="{on}">
                        <VBtn v-on="on">
                            <VIcon>mdi-format-underline</VIcon>
                        </VBtn>
                    </template>
                    
                </VTooltip>

                <VTooltip top>
                    <template v-slot:activator="{on}">
                        <VBtn v-on="on">
                            <VIcon>mdi-format-strikethrough</VIcon>
                        </VBtn>
                    </template>
                    
                </VTooltip>

                <VTooltip top>
                    <template v-slot:activator="{on}">
                        <VBtn v-on="on">
                            <VIcon>mdi-format-overline</VIcon>
                        </VBtn>
                    </template>
                    
                </VTooltip>
            </VBtnToggle>

            <VBtnToggle v-model="formatColors" multiple class="mr-1 mb-2">
                <ColorButton
                        icon="mdi-format-color-text"
                        v-model="localColor"
                        tooltip=" "
                />

                <ColorButton
                        icon="mdi-format-color-fill"
                        v-model="localBackgroundColor"
                        tooltip=" "
                >
                    <template v-slot:actions="props">
                        <VBtn small @click="turnOffBgColor(props)">
                             
                        </VBtn>
                    </template>
                </ColorButton>
            </VBtnToggle>


            <VExpandTransition>
                <VBtnToggle v-model="alignment" v-if="isMultiline" class="mb-2">
                    <VBtn>
                        <VIcon>mdi-format-align-left</VIcon>
                    </VBtn>

                    <VBtn>
                        <VIcon>mdi-format-align-center</VIcon>
                    </VBtn>

                    <VBtn>
                        <VIcon>mdi-format-align-right</VIcon>
                    </VBtn>
                </VBtnToggle>
            </VExpandTransition>

            <VSlider
                    label=""
                    min="0"
                    max="1"
                    step="0.1"
                    v-model="localOpacity"
            />

            <VSlider
                    label=""
                    v-model.number="localCharSpacing"
                    min="0"
                    max="1000"
            />

            <VExpandTransition>
                <VSlider
                        v-if="isMultiline"
                        label=""
                        min="0.1"
                        max="10"
                        :step="0.1"
                        v-model="localLineHeight"
                />
            </VExpandTransition>

            <VCheckbox
                    label=""
                    v-model="shadow"
            />

            <VExpandTransition>
                <VRow v-if="shadow" dense>
                    <VCol cols="4">
                        <div class="mt-2">
                            <ColorInput
                                    label=""
                                    v-model="localShadowColor"
                            />
                        </div>
                    </VCol>

                    <VCol cols="4">
                        <VTextField
                                label=" "
                                type="number"
                                min="-1000"
                                max="1000"
                                step="1"
                                v-model.number="localShadowXOffset"
                        />
                    </VCol>

                    <VCol cols="4">
                        <VTextField
                                label=" "
                                type="number"
                                min="-1000"
                                max="1000"
                                step="1"
                                v-model.number="localShadowYOffset"
                        />
                    </VCol>


                    <VDivider/>

                    <VSlider
                            label=""
                            min="0"
                            max="100"
                            step="1"
                            v-model="localShadowBlur"
                    />
                </VRow>
            </VExpandTransition>

            <VCheckbox
                    label=""
                    v-model="stroke"
            />

            <VExpandTransition>
                <div v-if="stroke">
                    <ColorInput
                            label=""
                            v-model="localStrokeColor"
                    />

                    <VSlider
                            label=""
                            min="0.5"
                            max="30"
                            step="0.5"
                            v-model="localStrokeWidth"
                    />
                </div>
            </VExpandTransition>
        </VCardText>
    </VCard>
</template>

<script>
  import {createPropAndDataWatchers} from '../../../../util';
  import ColorInput from '../../../color-input';
  import layerShadowMixin from './layer-shadow-mixin';
  import layerStrokeMixin from './layer-stroke-mixin';
  import ColorButton from './color-button';

  export default {
    name: 'text-layer',

    mixins: [layerShadowMixin, layerStrokeMixin],

    components: {
      ColorInput,
      ColorButton
    },

    props: {
      name: String,
      text: String,
      fontSize: Number,
      color: String,
      charSpacing: Number,
      fontFamily: String,
      fontWeight: String,
      fontStyle: String,
      underline: Boolean,
      linethrough: Boolean,
      overline: Boolean,
      textAlign: String,
      lineHeight: Number,
      opacity: Number,
      backgroundColor: {
        type: [String, null],
        default: null
      },
      fontFamilyNames: Array
    },

    data() {
      return {
        format: [],
        formatColors: [],
        alignment: 0,
        localText: '',
        localFontSize: null,
        localColor: null,
        localCharSpacing: null,
        localFontFamily: null,
        localLineHeight: null,
        localOpacity: null,
        localBackgroundColor: '#fff'
      };
    },

    computed: {
      fonts() {
        return [{
          value: 'serif',
          text: 'Serif'
        }, {
          value: 'sans-serif',
          text: 'Sans-Serif'
        }].concat(this.fontFamilyNames.map(ff => ({value: ff, text: ff})));
      },

      isMultiline() {
        return /\n/.test(this.localText);
      },

      formatDense() {
        switch (this.$vuetify.breakpoint.name) {
          case 'xs': return true
          case 'sm': return true
          case 'md': return true
          case 'lg': return false
          case 'xl': return false
        }
      }
    },

    watch: {
      ...createPropAndDataWatchers(['text', 'fontSize', 'color', 'charSpacing', 'fontFamily', 'lineHeight', 'opacity']),

      backgroundColor: {
        handler(newColor) {
          if (newColor) {
            if (newColor !== this.localBackgroundColor) {
              this.localBackgroundColor = newColor;
            }

            if (this.formatColors.indexOf(1) === -1) {
              this.formatColors.push(1);
            }
          } else if (this.formatColors.indexOf(1) !== -1) {
            this.formatColors.splice(this.formatColors.indexOf(1), 1);
          }
        },
        immediate: true
      },

      localBackgroundColor(newColor) {
        if (newColor && (!this.backgroundColor || this.backgroundColor !== newColor)) {
          this.$emit('update:background-color', newColor);
        } else if (!newColor && this.backgroundColor) {
          this.$emit('update:background-color', null);
        }
      },

      fontWeight: {
        handler(newVal, oldVal) {
          if (newVal !== oldVal) {
            if (newVal === 'bold' && this.format.indexOf(0) === -1) {
              this.format.push(0);
            } else if (newVal !== 'bold' && this.format.indexOf(0) !== -1) {
              this.format.splice(this.format.indexOf(0), 1);
            }
          }
        },

        immediate: true
      },

      fontStyle: {
        handler(newVal, oldVal) {
          if (newVal !== oldVal) {
            if (newVal === 'italic' && this.format.indexOf(1) === -1) {
              this.format.push(1);
            } else if (newVal !== 'italic' && this.format.indexOf(1) !== -1) {
              this.format.splice(this.format.indexOf(1), 1);
            }
          }
        },

        immediate: true
      },

      underline: {
        handler(newVal) {
          if (newVal && this.format.indexOf(2) === -1) {
            this.format.push(2);
          } else if (!newVal && this.format.indexOf(2) !== -1) {
            this.format.splice(this.format.indexOf(2), 1);
          }
        },

        immediate: true
      },

      linethrough: {
        handler(newVal) {
          if (newVal && this.format.indexOf(3) === -1) {
            this.format.push(3);
          } else if (!newVal && this.format.indexOf(3) !== -1) {
            this.format.splice(this.format.indexOf(3), 1);
          }
        },

        immediate: true
      },

      overline: {
        handler(newVal) {
          if (newVal && this.format.indexOf(4) === -1) {
            this.format.push(4);
          } else if (!newVal && this.format.indexOf(4) !== -1) {
            this.format.splice(this.format.indexOf(4), 1);
          }
        },

        immediate: true
      },

      format(newVal) {
        if (newVal.indexOf(0) !== -1 && this.fontWeight !== 'bold') {
          this.$emit('update:font-weight', 'bold');
        } else if (newVal.indexOf(0) === -1 && this.fontWeight !== 'normal') {
          this.$emit('update:font-weight', 'normal');
        }

        if (newVal.indexOf(1) !== -1 && this.fontStyle !== 'italic') {
          this.$emit('update:font-style', 'italic');
        } else if (newVal.indexOf(1) === -1 && this.fontStyle !== 'normal') {
          this.$emit('update:font-style', 'normal');
        }

        if (newVal.indexOf(2) !== -1 && !this.underline) {
          this.$emit('update:underline', true);
        } else if (newVal.indexOf(2) === -1 && this.underline) {
          this.$emit('update:underline', false);
        }

        if (newVal.indexOf(3) !== -1 && !this.linethrough) {
          this.$emit('update:linethrough', true);
        } else if (newVal.indexOf(3) === -1 && this.linethrough) {
          this.$emit('update:linethrough', false);
        }

        if (newVal.indexOf(4) !== -1 && !this.overline) {
          this.$emit('update:overline', true);
        } else if (newVal.indexOf(4) === -1 && this.overline) {
          this.$emit('update:overline', false);
        }
      },

      formatColors(newVal) {
        if (newVal.indexOf(0) !== -1) {
          this.formatColors.splice(this.formatColors.indexOf(0), 1);
        }

        if (newVal.indexOf(1) !== -1) {
          if (this.backgroundColor === null) {
            this.formatColors.splice(this.formatColors.indexOf(1), 1);
          }
        } else {
          if (this.backgroundColor !== null) {
            this.formatColors.push(1);
          }
        }
      },

      textAlign: {
        handler(newVal) {
          var alignment = 0;

          switch (newVal) {
            case 'left':
              alignment = 0;
              break;
            case 'center':
              alignment = 1;
              break;
            case 'right':
              alignment = 2;
              break;
          }

          if (alignment !== this.alignment) {
            this.alignment = alignment;
          }
        },
        immediate: true
      },

      alignment(newVal) {
        let alignments = ['left', 'center', 'right'];
        let textAlign = alignments[newVal];

        if (textAlign !== this.textAlign) {
          this.$emit('update:text-align', textAlign);
        }
      }
    },

    methods: {
      turnOffBgColor(colorButtonProps) {
        colorButtonProps.closeMenu();
        this.$emit('update:background-color', null);
      }
    }
  }
</script>