<template>
    <div class="caman-canvas-scaler">
        <CamanCanvas
                ref="canvas"
                v-if="canvasScale"
                :width="width"
                :height="height"
                :image="image"
                :filter="filter"
                class="caman-canvas-scaler__canvas"
                :style="canvasStyle"
                @applying="$emit('applying')"
                @applied="$emit('applied')"
        />
    </div>
</template>

<script>
  import {debounce} from 'lodash';
  import CamanCanvas from './caman-canvas';

  export default {
    name: 'fabric-canvas-scaler',

    components: {
      CamanCanvas
    },

    props: {
      width: {
        type: Number,
        default: 300
      },

      height: {
        type: Number,
        default: 150
      },

      image: String,

      filter: [String, null]
    },

    data() {
      return {
        canvasScale: 0,
        offsetTop: 0,
        offsetLeft: 0
      }
    },

    computed: {
      canvasStyle() {
        return {
          transform: `matrix(${this.canvasScale}, 0, 0, ${this.canvasScale}, ${this.offsetLeft}, ${this.offsetTop})`
        };
      }
    },

    mounted() {
      this.debouncedScaleToFit = debounce(() => this.scaleToFit(), 300);
      this.scaleToFit();
      this.resizeHandler = event => this.debouncedScaleToFit();
      window.addEventListener('resize', this.resizeHandler);
    },

    beforeDestroy() {
      window.removeEventListener('resize', this.resizeHandler);
    },

    methods: {
      scaleToFit() {
        this.canvasScale = 0;
        let {width, height, top, left} = this.$el.getBoundingClientRect();
        let {width: canvasWidth, height: canvasHeight} = this;
        let ratio = canvasWidth / canvasHeight;

        if (canvasWidth > width) {
          canvasWidth = width;
          canvasHeight = canvasWidth / ratio;
        }

        if (canvasHeight > height) {
          canvasHeight = height;
          canvasWidth  = canvasHeight * ratio;
        }

        this.canvasScale = canvasWidth / this.width;
        this.offsetTop = height / 2 - canvasHeight / 2;
        this.offsetLeft = width / 2 - canvasWidth / 2;
      },

      toDataUrl() {
        return this.$refs.canvas.toDataUrl();
      }
    }
  };
</script>

<style scoped>
    .caman-canvas-scaler {
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
    }

    .caman-canvas-scaler__canvas {
        position: absolute;
        transform-origin: 0 0;
    }

</style>