import {createPropAndDataWatchers} from '../../../../util';

export default {
  props: {
    strokeColor: {
      type: String,
      default: '#000'
    },
    strokeWidth: Number
  },

  data() {
    return {
      stroke: false,
      localStrokeColor: null,
      localStrokeWidth: null
    };
  },

  watch: {
    ...createPropAndDataWatchers(['strokeColor']),

    strokeWidth: {
      handler(newWidth) {
        if (newWidth !== this.localStrokeWidth) {
          this.localStrokeWidth = newWidth;
        }

        this.stroke = !!newWidth;
      },

      immediate: true
    },

    localStrokeWidth(newWidth) {
      if (!this.stroke) {
        newWidth = 0;
      }

      if (newWidth !== this.strokeWidth) {
        this.$emit('update:stroke-width', newWidth);
      }
    },

    stroke(val) {
      if (!val && this.strokeWidth) {
        this.$emit('update:stroke-width', 0);
      } else if (val && this.strokeWidth !== this.localStrokeWidth) {
        this.$emit('update:stroke-width', this.localStrokeWidth);
      }
    }
  }
};