<template>
    <div class="fabric-canvas-scaler">
        <VFabricStaticCanvas
                v-if="canvasScale && staticCanvas"
                ref="canvas"
                :width="canvasWidth"
                :height="canvasHeight"
                :zoom="canvasScale"
                style="position: absolute;"
                class="fabric-canvas-scaler__canvas d-flex align-center align-self-center justify-center"
        >
            <slot/>
        </VFabricStaticCanvas>

        <VFabricCanvas
                v-else-if="canvasScale"
                ref="canvas"
                :width="canvasWidth"
                :height="canvasHeight"
                :zoom="canvasScale"
                style="position: absolute;"
                class="fabric-canvas-scaler__canvas d-flex align-center align-self-center justify-center"
        >
            <slot/>
        </VFabricCanvas>
    </div>
</template>

<script>
  import VFabricCanvas from '../../v-fabric/v-fabric-canvas';
  import VFabricStaticCanvas from '../../v-fabric/v-fabric-static-canvas';
  import {debounce} from 'lodash';

  export default {
    name: 'fabric-canvas-scaler',

    components: {
      VFabricCanvas,
      VFabricStaticCanvas
    },

    props: {
      width: {
        type: Number,
        default: 300
      },

      height: {
        type: Number,
        default: 150
      },

      staticCanvas: {
        type: Boolean,
        default: false
      }
    },

    data() {
      return {
        canvasWidth: this.width,
        canvasHeight: this.height,
        canvasScale: 0
      }
    },

    mounted() {
      this.debouncedScaleToFit = debounce(() => this.scaleToFit(), 300);
      this.scaleToFit();
      this.resizeHandler = event => this.debouncedScaleToFit();
      window.addEventListener('resize', this.resizeHandler);
    },

    beforeDestroy() {
      window.removeEventListener('resize', this.resizeHandler);
    },

    methods: {
      scaleToFit() {
        this.canvasScale = 0;
        let {width, height} = this.$el.getBoundingClientRect();
        let {width: canvasWidth, height: canvasHeight} = this;
        let ratio = canvasWidth / canvasHeight;

        if (canvasWidth > width) {
          canvasWidth = width;
          canvasHeight = canvasWidth / ratio;
        }

        if (canvasHeight > height) {
          canvasHeight = height;
          canvasWidth = canvasHeight * ratio;
        }

        this.canvasWidth = canvasWidth;
        this.canvasHeight = canvasHeight;
        this.canvasScale = canvasWidth / this.width;
      },

      toDataUrl(options = {}) {
        return this.$refs.canvas.toDataUrl(options);
      }
    }
  };
</script>

<style scoped>
    .fabric-canvas-scaler {
        width: 100%;
        height: 100%;
        position: relative;
    }

    .fabric-canvas-scaler__canvas {
        max-width: 100%;
        max-height: 100%;
        width: 100%;
        height: 100%;
        display: flex;
        align-content: center;
        justify-content: center;
    }

    ::v-deep .v-fabric__canvas.lower-canvas {
        background-color: #fff;
    }
</style>