<template>
    <div class="file-selection-area">
        <label :for="id" class="file-selection-area__label">
            <input
                    ref="input"
                    type="file"
                    :id="id"
                    accept="image/jpeg,image/png,image/gif"
                    @change="onFileInputChange"
                    class="file-selection-area__file-input"
                    :multiple="multiple"
                    :disabled="disabled"
            >

            <slot :click="openFileDialog"/>
        </label>

        <div
                v-show="displayDropZone"
                class="file-selection-area__drop-zone"
                @dragenter.prevent="onDropZoneDragenter"
                @dragover.prevent="onDropZoneDragover"
                @drop.prevent.stop="onDrop"
                @dragleave.prevent="onDropzoneDragleave"
        >
            <div ref="drop_zone" class="file-selection-area__drop-zone-inner">
                <slot name="drop-zone"/>
            </div>
        </div>
    </div>
</template>

<script>
  function hasFilesInDraggable(event) {
    if (!('items' in event.dataTransfer)) {
      // If items is not supported, return true
      return true;
    }

    for (var i = 0, item; item = event.dataTransfer.items[i]; i++) {
      if (item.kind.toLowerCase() === 'file') {
        return true;
      }
    }

    return false;
  }

  export default {
    name: 'FileSelectionArea',

    props:  {
      id:         String,
      multiple: Boolean,
      disabled: Boolean,
      enableDropZone: {
        type: Boolean,
        default: false
      }
    },

    data() {
      return {
        files:           [],
        displayDropZone:        false,
        dragenterCounter:       0
      }
    },

    mounted() {
      this.onDocDragEnter    = event => {
        if (!this.disabled && this.enableDropZone) {
          if (hasFilesInDraggable(event)) {
            this.showDropZone();
          }
        }
      };
      document.addEventListener('dragenter', this.onDocDragEnter);
    },

    beforeDestroy() {
      document.removeEventListener('dragenter', this.onDocDragEnter);
    },

    methods:    {
      openFileDialog() {
        this.$refs.input.click();
      },

      onFileInputChange(event) {
        this.handleFileSelection(event.target.files);
        this.$refs.input.value = '';
      },

      handleFileSelection(files) {
        if (!this.disabled) {
          if (files) {
            this.files      = Array.prototype.filter.call(files, file => /image\/jpe?g|png/.test(file.type));

            if (!this.files.length) {
              this.files = null;
            }
          } else {
            this.files      = null;
          }

          this.$emit('input', this.files);
        }
      },

      onDropZoneDragover(event) {
        event.preventDefault();
      },

      onDropZoneDragenter(event) {
        this.dragenterCounter++;
      },

      onDropzoneDragleave(event) {
        this.dragenterCounter--;

        if (!this.dragenterCounter) {
          this.hideDropZone();
        }
      },

      onDrop(event) {
        this.hideDropZone();
        this.dragenterCounter   = 0;


        this.handleFileSelection(event.dataTransfer.files);
      },

      showDropZone() {
        this.displayDropZone    = true;
      },

      hideDropZone() {
        this.displayDropZone    = false;
      }
    }
  }
</script>

<style scoped>
    .file-selection-area__label {
        display: block;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }

    .file-selection-area__file-input {
        opacity: 0;
        width: 1px;
        height: 1px;
        position: absolute;
    }

    .file-selection-area__drop-zone {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.3);
        z-index: 999;
        padding: 20px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .file-selection-area__drop-zone-inner {
        border: 2px dashed royalblue;
        height: 100%;
    }
</style>