import {kebabCase, upperFirst} from 'lodash';

export function preloadImage(src) {
  return new Promise((resolve, reject) => {
    const preloader = new Image();

    preloader.onload = () => {
      preloader.onload = null;
      preloader.onerror = null;
      resolve(preloader)
    };

    preloader.onerror = () => {
      preloader.onload = null;
      preloader.onerror = null;
      reject(new Error("Couldn't load image"))
    };

    preloader.src = src;
  });
}

export function loadImageFile(file, onprogress) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onloadend = () => resolve(reader.result);
    reader.onerror = () => reject(reader.error);

    if (onprogress) {
      reader.onprogress = event => {
        onprogress(event.loaded / event.total);
      };
    }

    reader.readAsDataURL(file);
  });
}

/**
 * Creates simple prop watcher that copies prop value into local data prop.
 * @param {string} dataName Data prop name to assign prop value to
 * @returns {Function}
 */
export function createPropWatcher(dataName) {
  return function (newValue, oldValue) {
    if (newValue !== oldValue && newValue !== this[dataName]) {
      this[dataName] = newValue;
    }
  }
}

/**
 * Creates simple local data prop watcher that emits update:* event for given prop when local value is updated
 * @param {string} propName Prop name to update
 * @returns {Function}
 */
export function createDataWatcher(propName) {
  return function (newValue, oldValue) {
    if (newValue !== oldValue && newValue !== this[propName]) {
      this.$emit('update:' + kebabCase(propName), newValue);
    }
  }
}

/**
 * Creates set of pairs of prop watcher and corresponding local data watcher for two-way data binding
 * @param propNames
 * @param propImmediate
 * @param dataImmediate
 */
export function createPropAndDataWatchers(propNames, propImmediate = true, dataImmediate = true) {
  const watchers = {};

  propNames.forEach(propName => {
    const dataName = 'local' + upperFirst(propName);
    watchers[propName] = {
      handler: createPropWatcher(dataName),
      immediate: propImmediate
    };
    watchers[dataName] = {
      handler: createDataWatcher(propName),
      immediate: dataImmediate
    }
  });

  return watchers;
}