<template>
    <VCard class="pa-1 ma-1">
        <VCardTitle>
            <span> </span>
            <VSpacer/>
            <VBtn icon color="red" dark @click="$emit('delete')">
                <VIcon>mdi-delete-forever</VIcon>
            </VBtn>
        </VCardTitle>

        <VCardText>
            <VSlider
                    label=""
                    min="0"
                    max="1"
                    step="0.1"
                    v-model="localOpacity"
            />

            <VCheckbox
                    label=""
                    v-model="shadow"
            />

            <VExpandTransition>
                <VRow v-if="shadow" dense>
                    <VCol cols="4">
                        <div class="mt-2">
                            <ColorInput
                                    label=""
                                    v-model="localShadowColor"
                            />
                        </div>
                    </VCol>

                    <VCol cols="4">
                        <VTextField
                                label=" "
                                type="number"
                                min="-1000"
                                max="1000"
                                step="1"
                                v-model.number="localShadowXOffset"
                        />
                    </VCol>

                    <VCol cols="4">
                        <VTextField
                                label=" "
                                type="number"
                                min="-1000"
                                max="1000"
                                step="1"
                                v-model.number="localShadowYOffset"
                        />
                    </VCol>


                    <VDivider/>

                    <VSlider
                            label=""
                            min="0"
                            max="100"
                            step="1"
                            v-model="localShadowBlur"
                    />
                </VRow>
            </VExpandTransition>

            <VCheckbox
                    label=""
                    v-model="stroke"
            />

            <VExpandTransition>
                <div v-if="stroke">
                    <ColorInput
                            label=""
                            v-model="localStrokeColor"
                    />

                    <VSlider
                            label=""
                            min="0.5"
                            max="30"
                            step="0.5"
                            v-model="localStrokeWidth"
                    />
                </div>
            </VExpandTransition>
        </VCardText>
    </VCard>
</template>

<script>
  import {createPropAndDataWatchers} from '../../../../util';
  import ColorInput from '../../../color-input';
  import layerShadowMixin from './layer-shadow-mixin';
  import layerStrokeMixin from './layer-stroke-mixin';

  export default {
    name: 'image-layer',

    mixins: [layerShadowMixin, layerStrokeMixin],

    components: {
      ColorInput
    },

    props: {
      name: String,
      image: Image,
      opacity: Number
    },

    data() {
      return {
        localOpacity: null
      };
    },

    watch: {
      ...createPropAndDataWatchers(['opacity'])
    }
  }
</script>