import objectMixinFactory from './object-mixin';
import shadowMixin from './shadow-mixin';
import strokeProps from './stroke-props';

const objectMixin = objectMixinFactory({
  props: {
    ...strokeProps
  }
});

export default {
  name: 'v-fabric-image',

  mixins: [objectMixin, shadowMixin],

  props: {
    image: Image,
  },

  data() {
    return {
      fabricConstructorName: 'Image'
    };
  },

  methods: {
    getFabricConstructorFirstArgument() {
      return this.image;
    }
  }
};