<template>
    <VContainer fluid fill-height align-stretch pa-0>
        <VRow class="fill-height" no-gutters>
            <VCol cols="5" md="4" lg="3">
                <InputPanel
                        :layers.sync="layers"
                        :font-family-names="fontFamilyNames"
                        :filter.sync="selectedFilter"
                        :filters="filters"
                        @add-image-layer="onFileSelected"
                        @add-text-layer="addTextLayer('')"
                        @bg-image-file-selected="onBackgroundImageFileSelected"
                        @bg-image-url-selected="onBackgroundImageUrlSelected"
                        @clear-bg="backgroundImage = null"
                        @remove-layer="removeLayer"
                        @enter-filters="onEnterFilters"
                        @leave-filters="onLeaveFilters"
                        :selected-layer="selectedLayer"
                        :filter-preview-image="filterPreviewImage"
                        :disable-filter-tab="disableFiltersTab"
                ></InputPanel>
            </VCol>

            <VCol cols="7" md="8" lg="9" class="fill-height">
                <WorkingArea
                        ref="working_area"
                        class="fill-height"
                        :layers="renderLayers"
                        :formats="formats"
                        :selected-format="selectedFormat"
                        :background-image="backgroundImage"
                        :filter="selectedFilter"
                        @layer-selected="onLayerSelected"
                        @layer-deselected="onLayerDeselected"
                        @format-selected="onFormatSelected"
                        @format-deselected="onFormatDeselected"
                        @filter-applying="onApplyingFilter"
                        @filter-applied="onAppliedFilter"
                        @saving-all="onSavingAll"
                        @saved-all="onSavedAll"
                        @error-saving-all="onErrorSavingAll"
                        :disable-formats-toggling="disableFormatSelection"
                        :filter-source-images="savedImages"
                        :is-filter-mode="isFilterMode"
                        @fabric-images-saving="onFabricImagesSaving"
                        @fabric-images-saved="onFabricImagesSaved"
                        @fabric-images-saving-error="onFabricImagesSavingError"
                />
            </VCol>
        </VRow>

        <VOverlay :value="showLoading">
            <VProgressCircular
                    :indeterminate="loadProgress === null"
                    :value="loadProgressPercentage"
                    :size="60"
            >
                {{ loadProgressPercentage }}
            </VProgressCircular>
        </VOverlay>
    </VContainer>
</template>

<script>
  import InputPanel from './input-panel/input-panel';
  import WorkingArea from './working-area/working-area';
  var WebFont = require('webfontloader');

  import {preloadImage, loadImageFile} from '../../util';

  export default {
    name: 'media-editor',

    components: {
      InputPanel,
      WorkingArea
    },

    props: {
      fontFamilies: {
        type: Array,
        default() {
          return [
            'Open Sans',
            'Oswald'
          ];
        }
      }
    },

    data() {
      return {
        // if something is loading
        loading: false,

        // loading progress value (if available)
        loadProgress: null,

        // if saving is performed
        isSavingAll: false,

        // layers of generated images
        layers: [],

        // background of generated images
        backgroundImage: null,

        // incrementing value for new layers
        layerId: 0,

        // formats of images being generated
        formats: [{
          title: 'Instagram',
          size: [1080, 1080],
          selected: true
        }, {
          title: 'Instagram Story',
          size: [1080, 1920],
          selected: true
        }, {
          title: 'Facebook',
          size: [1200, 630],
          selected: true
        }, {
          title: 'Twitter',
          size: [1024, 512],
          selected: true
        }, {
          title: 'LinkedIn',
          size: [1200, 627],
          selected: true
        }, {
          title: 'Pinterest',
          size: [1000, 1500],
          selected: true
        }],

        // available filters (from Caman.js)
        filters:            {
          vintage: 'Vintage',
          lomo: 'Lomo',
          clarity: 'Clarity',
          sinCity: 'Sin City',
          sunrise: 'Sunrise',
          crossProcess: 'Cross Process',
          orangePeel: 'Orange Peel',
          love: 'Love',
          grungy: 'Grungy',
          jarques: 'Jarques',
          pinhole: 'Pinhole',
          oldBoot: 'Old Boot',
          glowingSun: 'Glowing Sun',
          hazyDays: 'Hazy Days',
          herMajesty: 'Her Majesty',
          nostalgia: 'Nostalgia',
          hemingway: 'Hemingway',
          concentrate: 'Concentrate'
        },

        // selected filter (from Caman.js)
        selectedFilter:     null,

        // layer being edited
        selectedLayer:      null,

        // format being edited
        selectedFormat:     null,

        // preview image src to use in filter previews
        filterPreviewImage: null,

        // disables toggling between editing mode and previews list mode
        disableFormatSelection: false,

        // increment when filter is being applied to previews. loading overlay must be shown at that time.
        // decrements after each preview was filtered
        applyingFiltersCounter: 0,

        // images saved for applying filters
        savedImages: {},

        // result images
        savedResultImages: {},

        isFilterMode: false
      };
    },


    computed: {
      // layers to be added to canvas
      renderLayers() {
        return this.layers.filter(layer => {
          if (layer.type === 'text' && !layer.text) {
            // do not add empty text layers
            return false;
          }

          return true;
        });
      },

      // disables to navigate into `filters` tab (when nothing is rendered or in editing mode)
      disableFiltersTab() {
        return !this.backgroundImage && !this.renderLayers.length || !!this.selectedFormat;
      },

      // show loading overlay
      showLoading() {
        return this.loading || !!this.applyingFiltersCounter || this.isSavingAll;
      },

      // value or loading progress in percents (when available)
      loadProgressPercentage() {
        if (this.loadProgress === null) {
          return null;
        }

        return (this.loadProgress * 100).toFixed() + '%';
      },

      // Array of available font families to apply to text layers
      fontFamilyNames() {
        return this.fontFamilies.map(ff => ff.split(':')[0]);
      },
    },

    created() {
      // load fonts
      WebFont.load({
        google: {
          families: this.fontFamilies
        }
      });
    },

    mounted() {
      // setTimeout(() => {
      //
      // }, 8000);
    },

    watch: {
      selectedLayer(newLayer, oldLayer) {
        if (newLayer !== oldLayer && oldLayer && oldLayer.type === 'text' && !oldLayer.text) {
          // remove empty text layer when deselected
          this.removeLayer(oldLayer);
        }
      }
    },

    methods: {
      // load background image by url
      onBackgroundImageUrlSelected(url) {
        this.preloadImage(url).then(img => this.backgroundImage = img);
      },

      // load background image from user file
      onBackgroundImageFileSelected(files) {
        if (files) {
          this.preloadImageFile(files[0])
              .then(img => this.backgroundImage = img);

        }
      },

      // load image layer from user file
      onFileSelected(files) {
        if (files) {
          return this.preloadImageFile(files[0])
                     .then(img => this.addImageLayer(img));
        }
      },

      // return incremented value for new layer as ID
      makeLayerId() {
        return ++this.layerId;
      },

      // return layer's common properties
      makeLayerBase(name) {
        return {
          id: this.makeLayerId(),
          name,
          opacity: 1,
          shadowColor: null,
          shadowXOffset: 0,
          shadowYOffset: 0,
          shadowBlur: 0,
          stroke: '#000',
          strokeWidth: 0
        };
      },

      /**
       * Add text layer
       * @param text
       * @param name
       */
      addTextLayer(text = '', name = '') {
        this.addLayer({
          ...this.makeLayerBase(name),
          type: 'text',
          text,
          fontSize: 30,
          color: '#000',
          charSpacing: 0,
          fontFamily: 'sans-serif',
          fontWeight: 'normal',
          underline: false,
          linethrough: false,
          overline: false,
          fontStyle: 'normal',
          textAlign: 'left',
          strokeWidth: 0,
          lineHeight: 1,
          textBackgroundColor: null
        });
      },

      /**
       * Add image layer
       * @param image
       * @param name
       */
      addImageLayer(image, name = '') {
        this.addLayer({
          ...this.makeLayerBase(name),
          type: 'image',
          image
        });
      },

      /**
       * Add new layer to layers array
       * @param layer
       */
      addLayer(layer) {
        this.layers.push(layer);
        this.selectedLayer = layer;
      },

      /**
       * Loads image file as data-url, displaying progress
       * @param {File} file
       * @returns {Promise<String>}
       */
      loadImageFile(file) {
        this.loading = true;
        this.loadProgress = 0;

        return loadImageFile(file, progress => {
          this.loadProgress = progress;
        }).finally(() => {
          this.loading = false;
          this.loadProgress = null;
        });
      },

      /**
       * Preloads image displaying loader
       * @param {String} src
       * @returns {Promise<Image>}
       */
      preloadImage(src) {
        this.loading  = true;
        return preloadImage(src)
          .finally(() => this.loading = false);
      },

      /**
       * Loads image from file and preloads it into DOM Image, displaying loader
       * @param {File} file
       * @returns {Promise<Image>}
       */
      preloadImageFile(file) {
        return this.loadImageFile(file)
          .then(src => this.preloadImage(src));
      },

      /**
       * Removes given layer
       * @param layer
       */
      removeLayer(layer) {
        if (layer === this.selectedLayer) {
          this.selectedLayer = null;
        }
        this.layers.splice(this.layers.indexOf(layer), 1);
      },

      /**
       * Called when user selects layer on canvas
       * @param layer
       */
      onLayerSelected(layer) {
        // set layer as selected
        this.selectedLayer = layer;

        // move layer to the end of layers array (unused feature since layer sorting is not implemented)
        let index = this.layers.indexOf(layer);
        if (index !== this.layers.length - 1) {
          this.layers.splice(index, 1);
          this.layers.push(layer);
        }
      },

      /**
       * Called when user deselects layer on canvas
       * @param layer
       */
      onLayerDeselected(layer) {
        this.selectedLayer = null;
      },

      /**
       * Called when user selects format preview to edit
       * @param format
       */
      onFormatSelected(format) {
        if (!this.disableFormatSelection) {
          this.selectedFormat = format;
        }
      },

      /**
       * Called when user returns from edit view to previews list
       */
      onFormatDeselected() {
        this.selectedFormat = null;
      },

      /**
       * Called when user opens `filters` tab
       */
      onEnterFilters() {
        // disable format selection to prevent user open image for editing, since filter is applied slowly
        this.disableFormatSelection = true;

        this.isFilterMode = true;

        // deselect format and current layer
        this.selectedFormat = null;
        this.selectedLayer = null;


        // this.$nextTick(() => {
        //   // create small previews for filter tab
        //   this.$refs.working_area.saveAll({multiplier: 0.15}).then(([firstPreview]) => {
        //     this.filterPreviewImage = firstPreview;
        //   })
        // });
      },


      /**
       * Called when user leaves `filters` tab
       */
      onLeaveFilters() {
        // enable image editing
        this.disableFormatSelection = false;

        this.isFilterMode = false;
        // deselect filter to revert changes since we can't edit filtered images
        this.selectedFilter = null;
      },

      /**
       * Called when filter is being applied to previews for each preview
       */
      onApplyingFilter() {
        // increment counter to show loading overlay
        this.applyingFiltersCounter++;
      },

      /**
       * Called when filter is applied to preview for each preview
       */
      onAppliedFilter() {
        // decrement counter to hide loading overlay when it's zero
        this.applyingFiltersCounter--;
      },

      /**
       * Called when user click `save all`
       */
      onSavingAll() {
        // display loading overlay
        this.isSavingAll = true;
      },

      /**
       * Called when all canvases are saved as data-urls
       * @param {String[]} srcs Array of data-urls
       */
      onSavedAll(images) {
        // TODO: convert to blobs and save as data-prop
        this.isSavingAll = false;
        this.savedResultImages = images;
        let win = window.open();
        Object.keys(images).forEach(formatTitle => {
          const src = images[formatTitle];
          win.document.write(`<img src="${src}">`);
        });

        win.document.close();
      },

      /**
       * Called when error in saving
       * @param error
       */
      onErrorSavingAll(error) {
        this.isSavingAll = false;
      },

      onFabricImagesSaving() {
        this.loading = true;
      },

      onFabricImagesSaved(images) {
        this.loading = false;
        this.savedImages = images;
        this.resizeForFilterPreview(images[Object.keys(images)[0]], 300, 300)
          .then(src => this.filterPreviewImage = src);
      },

      onFabricImagesSavingError(err) {
        this.loading = false;
      },

      resizeForFilterPreview(src, maxWidth, maxHeight) {
        return preloadImage(src)
          .then(img => {
            let width = img.width, height = img.height, ratio = width / height;

            if (width > maxWidth) {
              width = maxWidth;
              height = width / ratio;
            }

            if (height > maxHeight) {
              height = maxHeight;
              width = height * ratio;
            }

            const canvas = document.createElement('canvas');
            canvas.width = width;
            canvas.height = height;
            canvas.getContext('2d').drawImage(img, 0, 0, img.width, img.height, 0, 0, width, height);
            return canvas.toDataURL();
          });
      },
    }
  }
</script>