import {upperFirst} from 'lodash';

function makeDeferred() {
  const deferred = {};

  deferred.promise = new Promise(resolve => {
    deferred.resolve = resolve;
  });

  return deferred;
};

function makeDimensionHandler(dimensionName) {
  const localDimensionName = 'local' + upperFirst(dimensionName),
        setterMethod = 'set' + upperFirst(dimensionName);

  return function (newValue) {
    if (newValue !== this[localDimensionName]) {
      this[localDimensionName] = newValue;

      this.deferred.promise.then(canvas => {
        canvas[setterMethod](newValue);
        canvas.calcOffset();
      });
    }
  };
};

export default {
  props: {
    width: {
      type: [Number, String],
      default: 300
    },

    height: {
      type: [Number, String],
      default: 150
    },

    zoom: Number
  },

  data() {
    return {
      fabricCanvas: null,

      localWidth: null,

      localHeight: null,

      deferred: makeDeferred()
    };
  },

  provide() {
    return {
      promiseFabricCanvas: () => this.deferred.promise
    };
  },

  mounted() {
    this.fabricCanvas = this.buildCanvasObject();
    this.deferred.resolve(this.fabricCanvas);
  },

  beforeDestroy() {
    return this.deferred.promise.then(canvas => canvas.dispose());
  },

  watch: {
    width: {
      handler: makeDimensionHandler('width'),
      immediate: true
    },

    height: {
      handler: makeDimensionHandler('height'),
      immediate: true
    },

    zoom(newZoom) {
      if (newZoom) {
        this.deferred.promise.then(canvas => canvas.setZoom(newZoom));
      }
    }
  },

  methods: {
    buildCanvasObject() {},

    buildCanvasOptions() {
      const options = {};

      return options;
    },

    toDataUrl(options = {}) {
      let optionsCopy = Object.assign({},{multiplier: 1}, options);

      if (this.zoom) {
        optionsCopy.multiplier /= this.zoom;
      }

      return this.deferred.promise.then(canvas => {
        return canvas.toDataURL(optionsCopy);
      });
    }
  }
}