<template>
    <canvas
            ref="canvas"
            :width="width"
            :height="height"
    ></canvas>
</template>

<script>

  import {preloadImage} from '../../util';

  function makeDeferred() {
    const deferred = {};

    deferred.promise = new Promise(resolve => {
      deferred.resolve = resolve;
    });

    return deferred;
  };

  export default {
    name: 'caman-canvas',

    props: {
      width: {
        type: Number,
        default: 300
      },

      height: {
        type: Number,
        default: 150
      },

      scale: {
        type: Number,
        default: 1
      },

      image: String,

      filter: [String, null]
    },

    data() {
      return {
        deferred: makeDeferred(),
        initialised: false
      };
    },

    computed: {
      canvasStyle() {
        return {
          transform: `scale(${this.scale}, ${this.scale})`
        };
      }
    },

    watch: {
      image: {
        handler(newImage, oldImage) {
          if (newImage !== oldImage) {
            if (newImage) {
              this.setImageToCanvas(newImage)
                .then(() => {
                  if (!this.initialised) {
                    return this.initCaman();
                  } else {
                    return this.reloadCanvasData();
                  }
                });
            }
          }
        },
        immediate: true
      },

      filter(newFilter, oldFilter) {
        if (newFilter !== oldFilter) {
          if (!newFilter) {
            return this.revertFilter(true);
          } else {
            if (oldFilter) {
              return this.revertFilter().then(() => this.applyFilter(newFilter));
            }
            else {
              return this.applyFilter(newFilter);
            }
          }
        }
      }
    },

    mounted() {

    },

    methods: {
      setImageToCanvas(src) {
        return preloadImage(src)
          .then(img => {
            this.$refs.canvas.getContext('2d').drawImage(img, 0, 0);
          });
      },

      reloadCanvasData() {
        return this.deferred.promise.then(caman => caman.reloadCanvasData());
      },

      initCaman() {
        this.initialised = true;
        let vm = this;
        Caman(this.$refs.canvas, function () {
          vm.deferred.resolve(this);
        });
      },

      applyFilter(filterName) {
        this.$emit('applying');

        return this.deferred.promise.then(caman => {
          return new Promise(resolve => {
            caman[filterName]().render(() => {
              this.$emit('applied');
              resolve(caman);
            });
          });
        });
      },

      revertFilter(render = false) {
        return this.deferred.promise.then(caman => {
          caman.revert(render);
          return caman;
        });
      },

      toDataUrl() {
        return this.$refs.canvas.toDataURL();
      }
    }
  }
</script>