<template>
    <VImg v-if="preview" :src="preview" :aspect-ratio="1" max-width="100%" gradient="to bottom, rgba(0,0,0,.8), transparent 50%">
        <slot/>
    </VImg>
</template>

<script>
  import {preloadImage} from '../../../../util';

  export default {
    name: 'filter-preview',

    props: {
      image: [String, null],
      filterName: String
    },

    data() {
      return {
        preview: null
      };
    },

    watch: {
      image: {
        handler(newImage, oldImage) {
          if (newImage !== oldImage) {
            if (!newImage) {
              this.preview = null;
            } else {
              this.applyFilter(newImage).then(src => this.preview = src);
            }
          }
        },

        immediate: true
      }
    },

    methods: {
      applyFilter(src) {
        return preloadImage(src)
          .then(img => {
            const canvas = document.createElement('canvas');
            canvas.width = img.width;
            canvas.height = img.height;
            canvas.getContext('2d').drawImage(img, 0, 0);

            return new Promise((resolve) => {
              var filterName = this.filterName;
              Caman(canvas, function () {
                this[filterName]().render(() => resolve(canvas.toDataURL()));
              });
            });
          });
      }
    }
  }
</script>