<template>
    <div class="working-area d-flex flex-column blue-grey lighten-5">
        <VToolbar class="flex-grow-0" color="blue-grey" flat>
            <template v-if="selectedFormat">
                <VToolbarTitle class="white--text">
                    {{ selectedFormat.title }} ({{ selectedFormat.size[0] }}x{{ selectedFormat.size[1] }})
                </VToolbarTitle>

                <VSpacer/>

                <VTooltip bottom>
                    <template v-slot:activator="{on}">
                        <VBtn
                                v-on="on"
                                @click="unselectFormat"
                        >
                            <VIcon>mdi-view-list</VIcon>
                        </VBtn>
                    </template>
                      
                </VTooltip>
            </template>

            <template v-else>
                <VMenu
                        v-if="!disableFormatsToggling"
                        :close-on-content-click="false"
                        v-model="displayFormatsMenu"
                >
                    <template v-slot:activator="{on: menuOn}">
                        <VTooltip bottom>
                            <template v-slot:activator="{on: tooltipOn}">
                                <VBtn
                                        dark
                                        icon
                                        v-on="{...menuOn, ...tooltipOn}"
                                        @click="unselectFormat"
                                >
                                    <VIcon>mdi-format-list-checks</VIcon>
                                </VBtn>
                            </template>
                             /  
                        </VTooltip>
                    </template>

                    <VCard>
                        <VCardText>
                            <VCheckbox
                                    v-for="format in formats"
                                    :key="format.title"
                                    :label="formatTitle(format)"
                                    v-model="format.selected"
                                    :disabled="format.selected && selectedFormats.length < 2"
                            />
                        </VCardText>

                        <VCardActions>
                            <VBtn @click="displayFormatsMenu = false">
                                Ok
                            </VBtn>
                        </VCardActions>
                    </VCard>
                </VMenu>

                <VSpacer/>

                <VBtn color="green" dark @click="onSaveAll">
                    <VIcon>mdi-content-save</VIcon>
                     
                </VBtn>
            </template>




        </VToolbar>

        <div class="working-area__viewport flex-grow-1 flex-shrink-1 align-start">
            <VFadeTransition>
                <FabricCanvasScaler
                        v-if="selectedFormat"
                        :width="selectedFormat.size[0]"
                        :height="selectedFormat.size[1]"
                        key="editor-canvas"
                        class="fill-height"
                >
                    <template v-for="(layer, index) in layers">
                        <VFabricImage
                                v-if="layer.type === 'image'"
                                :key="layer.id"
                                :image="layer.image"
                                :opacity="layer.opacity"
                                :shadow-color="layer.shadowColor"
                                :shadow-x-offset="layer.shadowXOffset"
                                :shadow-y-offset="layer.shadowYOffset"
                                :shadow-blur="layer.shadowBlur"
                                :stroke="layer.stroke"
                                :stroke-width="layer.strokeWidth"
                                :top.sync="layersAugments[selectedFormat.title][layer.id].top"
                                :left.sync="layersAugments[selectedFormat.title][layer.id].left"
                                :scale-x.sync="layersAugments[selectedFormat.title][layer.id].scaleX"
                                :scale-y.sync="layersAugments[selectedFormat.title][layer.id].scaleY"
                                :angle.sync="layersAugments[selectedFormat.title][layer.id].angle"
                                @selected="onLayerSelected(layer)"
                                @deselected="onLayerDeselected(layer)"
                        />

                        <VFabricText
                                v-else-if="layer.type === 'text'"
                                :key="layer.id"
                                :text="layer.text"
                                :font-size="layer.fontSize"
                                :fill="layer.color"
                                :char-spacing="layer.charSpacing"
                                :font-family="layer.fontFamily"
                                :font-weight="layer.fontWeight"
                                :underline="layer.underline"
                                :linethrough="layer.linethrough"
                                :overline="layer.overline"
                                :font-style="layer.fontStyle"
                                :text-align="layer.textAlign"
                                :shadow-color="layer.shadowColor"
                                :shadow-x-offset="layer.shadowXOffset"
                                :shadow-y-offset="layer.shadowYOffset"
                                :shadow-blur="layer.shadowBlur"
                                :stroke="layer.stroke"
                                :stroke-width="layer.strokeWidth"
                                :line-height="layer.lineHeight"
                                :text-background-color="layer.textBackgroundColor"
                                :opacity="layer.opacity"
                                :top.sync="layersAugments[selectedFormat.title][layer.id].top"
                                :left.sync="layersAugments[selectedFormat.title][layer.id].left"
                                :scale-x.sync="layersAugments[selectedFormat.title][layer.id].scaleX"
                                :scale-y.sync="layersAugments[selectedFormat.title][layer.id].scaleY"
                                :angle.sync="layersAugments[selectedFormat.title][layer.id].angle"
                                @selected="onLayerSelected(layer)"
                                @deselected="onLayerDeselected(layer)"
                        />
                    </template>

                    <VFabricBackgroundImage
                            v-if="backgroundImage"
                            :image="backgroundImage"
                    />
                </FabricCanvasScaler>

                <VFadeTransition v-else key="previews-list" tag="div" group class="row fill-height row--dense px-1" leave-absolute>
                    <VCol
                            class="working-area__preview-col"
                            v-for="(format, i) in selectedFormats"
                            :key="format.title"
                            :cols="formatPreviewCols"
                            :offset="formatPreviewOffset(i)"
                            :class="formatPreviewClass"
                    >
                        <VCard
                                class="working-area__preview-card fill-height d-flex flex-column justify-start pa-1"
                                color="blue-grey lighten-2"
                        >
                            <FabricCanvasScaler
                                    v-if="!isFilterMode"
                                    static-canvas
                                    :width="format.size[0]"
                                    :height="format.size[1]"
                                    @click.native="selectFormat(format)"
                                    ref="preview"
                                    class="working-area__preview-canvas-container flex-grow-1"

                            >
                                <template v-for="(layer, index) in layers">
                                    <VFabricImage
                                            v-if="layer.type === 'image'"
                                            :key="layer.id"
                                            :image="layer.image"
                                            :opacity="layer.opacity"
                                            :shadow-color="layer.shadowColor"
                                            :shadow-x-offset="layer.shadowXOffset"
                                            :shadow-y-offset="layer.shadowYOffset"
                                            :shadow-blur="layer.shadowBlur"
                                            :stroke="layer.stroke"
                                            :stroke-width="layer.strokeWidth"
                                            :top="layersAugments[format.title][layer.id].top"
                                            :left="layersAugments[format.title][layer.id].left"
                                            :scale-x="layersAugments[format.title][layer.id].scaleX"
                                            :scale-y="layersAugments[format.title][layer.id].scaleY"
                                            :angle="layersAugments[format.title][layer.id].angle"
                                    />

                                    <VFabricText
                                            v-else-if="layer.type === 'text'"
                                            :key="layer.id"
                                            :text="layer.text"
                                            :font-size="layer.fontSize"
                                            :fill="layer.color"
                                            :char-spacing="layer.charSpacing"
                                            :font-family="layer.fontFamily"
                                            :font-weight="layer.fontWeight"
                                            :underline="layer.underline"
                                            :linethrough="layer.linethrough"
                                            :overline="layer.overline"
                                            :font-style="layer.fontStyle"
                                            :text-align="layer.textAlign"
                                            :shadow-color="layer.shadowColor"
                                            :shadow-x-offset="layer.shadowXOffset"
                                            :shadow-y-offset="layer.shadowYOffset"
                                            :shadow-blur="layer.shadowBlur"
                                            :stroke="layer.stroke"
                                            :stroke-width="layer.strokeWidth"
                                            :line-height="layer.lineHeight"
                                            :text-background-color="layer.textBackgroundColor"
                                            :opacity="layer.opacity"
                                            :top="layersAugments[format.title][layer.id].top"
                                            :left="layersAugments[format.title][layer.id].left"
                                            :scale-x="layersAugments[format.title][layer.id].scaleX"
                                            :scale-y="layersAugments[format.title][layer.id].scaleY"
                                            :angle="layersAugments[format.title][layer.id].angle"
                                    />
                                </template>

                                <VFabricBackgroundImage
                                        v-if="backgroundImage"
                                        :image="backgroundImage"
                                />

                            </FabricCanvasScaler>

                            <CamanCanvasScaler
                                    v-else
                                    ref="filterPreview"
                                    :width="format.size[0]"
                                    :height="format.size[1]"
                                    :image="filterSourceImages[format.title]"
                                    :filter="filter"
                                    @applying="$emit('filter-applying')"
                                    @applied="$emit('filter-applied')"
                            />

                            <VCardActions class="flex-grow-0">
                                <VSpacer/>
                                <VSheet color="yellow" class="px-2">
                                    {{ format.title }} ({{ format.size[0] }}x{{ format.size[1] }})
                                </VSheet>
                            </VCardActions>
                        </VCard>
                    </VCol>
                </VFadeTransition>
            </VFadeTransition>
        </div>
    </div>
</template>

<script>
  import FabricCanvasScaler from './fabric-canvas-scaler';
  import VFabricImage from '../../v-fabric/v-fabric-image';
  import VFabricText from '../../v-fabric/v-fabric-text';
  import VFabricBackgroundImage from '../../v-fabric/v-fabric-background-image';
  import CamanCanvasScaler from '../caman-canvas-scaler';

  import {preloadImage} from '../../../util';

  export default {
    name: 'working-area',

    components: {
      FabricCanvasScaler,
      VFabricImage,
      VFabricText,
      VFabricBackgroundImage,
      CamanCanvasScaler
    },

    props: {
      layers: Array,

      formats: Array,

      backgroundImage: {
        type: [Image, null],
        default: null
      },

      filter: [String, null],
      
      selectedFormat: [Object, null],

      disableFormatsToggling: {
        type: Boolean,
        defaulT: false
      },

      isFilterMode: Boolean,

      filterSourceImages: [Object, null]
    },

    data() {
      return {
        // layers scaling, positions and angles per each format
        layersAugments: {},

        // controls formats menu visibility
        displayFormatsMenu: false
      };
    },

    computed: {
      selectedFormats() {
        return this.formats.filter(format => format.selected);
      },

      formatTitle() {
        return format => `${format.title} (${format.size[0]}x${format.size[1]})`;
      },

      formatPreviewCols() {
        switch (this.selectedFormats.length) {
          case 6:
          case 5:
          case 3:
          default:
            return 4;
          case 4:
          case 2:
            return 6;
          case 1:
            return 12;
        }
      },

      formatPreviewOffset() {
        return index => {
          const formatsCount = this.selectedFormats.length;

          if (formatsCount === 5 && index === 3) {
            return 2;
          } else {
            return 0;
          }
        };
      },

      formatPreviewClass() {
        return this.selectedFormats.length > 3 ? 'h50p' : 'fill-height';
      }
    },

    mounted() {
      this.fillLayersAugments(this.formats, this.layers);
    },

    watch: {
      formats(newFormats) {
        this.fillLayersAugments(newFormats, this.layers);
      },

      layers(newLayers) {
        this.fillLayersAugments(this.formats, newLayers);
      },

      selectedFormats() {
        this.$nextTick(() => {
          this.$refs.preview.forEach(scaler => {
            scaler.scaleToFit();
          });
        });
      },

      isFilterMode(newVal) {
        // Flatten image layers and save for filter rendering
        if (newVal) {
          this.$emit('fabric-images-saving');
          return this.saveFabricImages()
                     .then(images => this.$emit('fabric-images-saved', images))
                     .catch(error => {
                       this.$emit('fabric-images-saving-error', error);
                       throw error;
                     });
        }
      }
    },

    methods: {
      selectFormat(format) {
        this.$emit('format-selected', format);
      },

      unselectFormat() {
        this.$emit('format-deselected');
      },

      fillLayersAugments(formats, layers) {
        formats.forEach(({title}) => {
          let oldAugments = [], newAugments = [];

          if (title in this.layersAugments) {
            oldAugments = this.layersAugments[title];
          }

          layers.forEach(({id}) => {
            if (oldAugments[id]) {
              newAugments[id] = oldAugments[id];
            } else {
              newAugments[id] = {
                top: 0,
                left: 0,
                scaleX: 1,
                scaleY: 1,
                angle: 0
              };
            }
          });

          this.$set(this.layersAugments, title, newAugments);
        });
      },

      onLayerSelected(layer) {
        this.$emit('layer-selected', layer);
      },

      onLayerDeselected(layer) {
        this.$emit('layer-deselected', layer);
      },

      saveFabricImages() {
        return new Promise(resolve => {
          const saved = {};
          const promises = [];

          this.$refs.preview.forEach((fabricCanvasScaler, index) => {
            const title = this.selectedFormats[index].title;
            promises.push(
              fabricCanvasScaler.toDataUrl().then(dataUrl => saved[title] = dataUrl)
            );
          });

          return Promise.all(promises).then(() => resolve(saved));
        });
      },

      saveFilteredImages() {
        const saved = {};
        this.$refs.filterPreview.forEach((camanCanvasScaler, index) => {
          const title = this.selectedFormats[index].title;
          saved[title] = camanCanvasScaler.toDataUrl();
        });
        return Promise.resolve(saved);
      },

      onSaveAll() {
        this.$emit('saving-all');
        let promise;

        if (this.isFilterMode) {
          promise = this.saveFilteredImages();
        } else {
          promise = this.saveFabricImages();
        }

        return promise.then(srcs => {
          this.$emit('saved-all', srcs);
          return srcs;
        }).catch(err => {
          this.$emit('error-saving-all', err);
          throw err;
        });
      },

      // saveAll(options = {}) {
      //   if (!this.$refs.preview || !this.$refs.preview.length) {
      //     return Promise.reject(new Error("Images not rendered"));
      //   }
      //
      //   let promises = [];
      //
      //   for (let i = 0; i < this.$refs.preview.length; i++) {
      //     promises.push(this.saveSingle(this.$refs.preview[i], options));
      //   }
      //
      //   return Promise.all(promises);
      // },
      //
      // saveSingle(canvas, options = {}) {
      //   return canvas.toDataUrl(options)
      //     .then(dataUrl => {
      //       if (!this.filter) {
      //         return dataUrl;
      //       } else {
      //         return this.applyCamanFilter(dataUrl, this.filter);
      //       }
      //     });
      // },
      //
      // applyCamanFilter(src, filterName) {
      //   return preloadImage(src)
      //     .then(img => {
      //       const canvas = document.createElement('canvas');
      //       canvas.width = img.width;
      //       canvas.height = img.height;
      //       canvas.getContext('2d').drawImage(img, 0, 0);
      //
      //       return new Promise(resolve => {
      //         setTimeout(() => {
      //           Caman(canvas, function () {
      //             this[filterName]().render(() => {
      //               resolve(canvas.toDataURL());
      //             });
      //           });
      //         }, 0);
      //       });
      //     });
      // }
    }
  }
</script>

<style scoped lang="scss">
    .working-area__viewport {
        flex-basis: 50%;
    }

    .working-area__preview-col {
        .h50p {
            height: 50%;
        }
    }

    .working-area__preview-canvas-container {
        cursor: pointer;
    }
</style>