import objectMixinFactory from './object-mixin';
import shadowMixin from './shadow-mixin';
import strokeProps from './stroke-props';

const objectMixin = objectMixinFactory({
  props: {
    ...strokeProps,

    fontSize: {
      type: Number,
      default: 70,
      option: true,
      initialize: true,
      watchable: true
    },

    fill: {
      type: String,
      default: '#000',
      option: true,
      initialize: true,
      watchable: true
    },

    charSpacing: {
      type: Number,
      default: 0,
      option: true,
      initialize: true,
      watchable: true
    },

    fontFamily: {
      type: String,
      default: 'sans-serif',
      option: true,
      initialize: true,
      watchable: true
    },

    fontWeight: {
      type: String,
      default: 'normal',
      option: true,
      initialize: true,
      watchable: true
    },

    underline: {
      type: Boolean,
      default: false,
      option: true,
      initialize: true,
      watchable: true
    },

    linethrough: {
      type: Boolean,
      default: false,
      option: true,
      initialize: true,
      watchable: true
    },

    overline: {
      type: Boolean,
      default: false,
      option: true,
      initialize: true,
      watchable: true
    },

    fontStyle: {
      type: String,
      default: 'normal',
      option: true,
      initialize: true,
      watchable: true
    },

    textAlign: {
      type: String,
      default: 'left',
      option: true,
      initialize: true,
      watchable: true
    },

    lineHeight: {
      type: Number,
      default: 1,
      option: true,
      initialize: true,
      watchable: true
    },

    textBackgroundColor: {
      type: [String, null],
      default: null,
      option: true,
      initialize: true,
      watchable: true
    }
  }
});

export default {
  name: 'v-fabric-text',

  mixins: [objectMixin, shadowMixin],

  props: {
    text: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      fabricConstructorName: 'Text'
    };
  },

  computed: {
    shadow() {
      if (this.shadowColor) {
        return `${this.shadowColor} ${this.shadowXOffset}px ${this.shadowYOffset}px ${this.shadowBlur}px`;
      }
    }
  },

  watch: {
    text(newText) {
      this.promiseFabricCanvas().then(canvas => {
        if (this.instance && newText !== this.instance.get('text')) {
          this.instance.set('text', newText);
          canvas.renderAll();
        }
      });
    }
  },

  methods: {
    getFabricConstructorFirstArgument() {
      return this.text;
    }
  }
}