import {kebabCase, union} from 'lodash';
import {fabric} from 'fabric';

export function createPropWatcher(optionName) {
  return function (newValue, oldValue) {
    if (newValue !== oldValue && newValue !== this.instance.get(optionName)) {
      this.promiseFabricCanvas().then(canvas => {
        this.instance.set(optionName, newValue);
        canvas.renderAll();
      });
    }
  };
};

function createProps(propDefinitions) {
  const props = {};

  Object.keys(propDefinitions).forEach(propName => {
    const definition = propDefinitions[propName];
    props[propName] = {
      type: definition.type
    };

    if ('default' in definition) {
      props[propName].default = definition.default;
    }
  });

  return props;
};

function createPropWatchers(propDefinitions) {
  const watchers = {};

  Object.keys(propDefinitions).forEach(propName => {
    const definition = propDefinitions[propName];

    if (definition.watchable) {
      let optionName = typeof definition.option === 'string' ? definition.option : propName;

      watchers[propName] = {
        handler: createPropWatcher(optionName)
      };
    }
  });

  return watchers;
};

const defaultObjectEvents = [
  'added',
  'removed',
  //'selected',
  'deselected',
  'moved',
  'sca',
  'rotated',
  'skewed',
  'rotating',
  'scaling',
  'skewing',
  'mousedown',
  'mouseup',
  'mouseover',
  'mouseout',
  'mousewheel',
  'mousedblclick',
  'dragover',
  'dragenter',
  'dragleave',
  'drop'
];

const defaultProps = {
  top: {
    type: Number,
    default: 0,
    option: true,
    initialize: true,
    watchable: true,
    updatable: true
  },

  left: {
    type: Number,
    default: 0,
    option: true,
    initialize: true,
    watchable: true,
    updatable: true
  },

  scaleX: {
    type: Number,
    default: 1,
    option: true,
    initialize: true,
    watchable: true,
    updatable: true
  },

  scaleY: {
    type: Number,
    default: 1,
    option: true,
    initialize: true,
    watchable: true,
    updatable: true
  },

  angle: {
    type: Number,
    default: 0,
    option: true,
    initialize: true,
    watchable: true,
    updatable: true
  },

  skewX: {
    type: Number,
    default: 0,
    option: true,
    initialize: true,
    watchable: true,
    updatable: true
  },

  skewY: {
    type: Number,
    default: 0,
    option: true,
    initialize: true,
    watchable: true,
    updatable: true
  },

  opacity: {
    type: Number,
    default: 1,
    option: true,
    initialize: true,
    watchable: true
  }
};

function objectMixinFactory(options) {
  let events = union(defaultObjectEvents, (options.events || []));
  let propDefinitions = Object.assign({}, defaultProps, (options.props || {}));


  return {
    inject: ['promiseFabricCanvas'],

    props: createProps(propDefinitions),

    data() {
      return {
        instance: null
      };
    },

    mounted() {
      this.instance = this.buildInstance();
      return this.promiseFabricCanvas().then(canvas => canvas.add(this.instance));
    },

    watch: createPropWatchers(propDefinitions),

    beforeDestroy() {
      return this.onBeforeDestroy();
    },

    methods: {
      onBeforeDestroy() {
        if (!this.instance) {
          return this.$nextTick(() => this.onBeforeDestroy());
        }

        return this.promiseFabricCanvas().then(fabricCanvas => {
          fabricCanvas.remove(this.instance);
          this.instance.off();
        });
      },

      buildInstance() {
        const instance = new fabric[this.fabricConstructorName](
          this.getFabricConstructorFirstArgument(),
          this.buildOptions()
        );

        this.bindEvents(instance);

        return instance;
      },

      getFabricConstructorFirstArgument() {},

      buildOptions() {
        const options = {};

        Object.keys(propDefinitions).forEach(propName => {
          const definition = propDefinitions[propName];

          if ('option' in definition && definition.initialize) {
            let optName = typeof definition.option === 'string' ? definition.option : propName;
            options[optName] = this[propName];
          }
        });

        return this.extendOptions(options);
      },

      extendOptions(options) {
        return options;
      },

      bindEvents(instance) {
        events.forEach(eventName => {
          instance.on(eventName, event => {
            this.$emit(eventName, event);
          });
        });

        instance.on('selected', event => {
          this.$emit('selected', event);
          this.promiseFabricCanvas().then(canvas => {
            canvas.bringToFront(this.instance);
          });
        });

        instance.on('modified', event => {
          this.$emit('modified', event);

          Object.keys(propDefinitions).forEach(propName => {
            const definition = propDefinitions[propName];
            if (definition.updatable) {
              let optionName = typeof definition.option === 'string' ? definition.option : propName;
              let optValue = this.instance.get(optionName);

              if (this[propName] !== optValue) {
                this.$emit('update:' + kebabCase(propName), optValue);
              }
            }
          });
        });

        this.extendEvents(instance);
      },

      extendEvents(instance) {}
    },

    render(h) {
      return this.$slots.default ? h('div', this.$slots.default) : null;
    }
  };
};

export default objectMixinFactory;