<template>
    <div class="pa-1">
        <VRow dense>
            <VCol cols="4">
                <VCard dark @click="$emit('selected', null)" :elevation="elevation(null)">
                    <VImg v-if="filterPreviewImage" :src="filterPreviewImage" gradient="to bottom, rgba(0,0,0,.8), transparent 50%">
                        <VCardActions>
                            <span :class="textClasses(null)">None</span>
                        </VCardActions>
                    </VImg>
                </VCard>
            </VCol>

            <VCol cols="4" v-for="(filterName, filter) in filters" :key="filter">
                <VCard dark @click="$emit('selected', filter)" :elevation="elevation(filter)">
                    <FilterPreview :image="filterPreviewImage" :filter-name="filter">
                        <VCardActions>
                            <span :class="textClasses(filter)">{{ filterName }}</span>
                        </VCardActions>
                    </FilterPreview>
                </VCard>
            </VCol>
        </VRow>
    </div>
</template>

<script>
  import FilterPreview from './filter-preview';

  export default {
    name: 'filter-properties',

    components: {
      FilterPreview
    },

    props: {
      filters: Object,
      selectedFilter: [String, null],
      filterPreviewImage: [String, null]
    },

    computed: {
      elevation() {
        return filter => filter === this.selectedFilter ? 3 : 0;
      },

      textClasses() {
        return filter => filter === this.selectedFilter ? 'font-weight-bold yellow--text' : 'white--text';
      }
    }
  }
</script>