<template>
    <div class="v-fabric">
        <canvas
                class="v-fabric__canvas"
                ref="canvas"
                :width="width"
                :height="height"
        ></canvas>

        <div style="display: none;">
            <slot></slot>
        </div>
    </div>
</template>

<script>
  import {fabric} from 'fabric';
  import canvasMixin from './canvas-mixin';

  export default {
    name: 'v-fabric-static-canvas',

    mixins: [canvasMixin],

    methods: {
      buildCanvasObject() {
        const canvas = new fabric.StaticCanvas(this.$refs.canvas, this.buildCanvasOptions());

        if (this.zoom) {
          canvas.setZoom(this.zoom);
        }

        return canvas;
      }
    }
  }
</script>