<template>
    <VMenu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :close-on-click="true"
            :return-value.sync="color"
            transition="scale-transition"
            offset-y
            min-width="290px"
            :disabled="disabled"
    >
        <template v-slot:activator="{ on }">
            <VInput
                    :label="label"
            >
                <div class="transparent-backdrop" v-on="on">
                    <div class="color-sample" :class="{disabled: !!disabled}" :style="{backgroundColor: sheetColor}"></div>
                </div>
            </VInput>
        </template>

        <VCard>
            <VColorPicker
                    v-model="color"
                    :hide-mode-switch="true"
                    mode="rgba"
            />

            <VCardActions>
                <VBtn small @click="$refs.menu.save(color)" color="primary">
                    Ok
                </VBtn>

                <VBtn small @click="menu = false">
                    
                </VBtn>
            </VCardActions>
        </VCard>
    </VMenu>
</template>

<script>
  export default {
    name: 'ColorInput',

    props: {
      value: {
        type: String,
        default: '#000'
      },

      label: String,

      disabled: Boolean
    },

    data() {
      return {
        menu: false,
        color: this.value
      };
    },

    computed: {
      sheetColor() {
        return this.color;
      }
    },

    watch: {
      value: {
        handler(newColor, oldColor) {
          if (newColor !== oldColor) {
            this.color = newColor;
          }
        },
        immediate: true
      },

      color(newColor, oldColor) {
        if (newColor !== oldColor) {
          this.$emit('input', newColor);
        }
      }
    }
  }
</script>

<style scoped lang="scss">
    .transparent-backdrop {
        background-image: url(data:image/png;base64,);
        background-repeat: repeat;
        width: 100%;
        height: 20px;
        margin-top: 10px;
        border: #aaa 1px solid;

        .color-sample {
            width: 100%;
            height: 100%;
            cursor: pointer;

            &.disabled {
                cursor: default;
            }
        }
    }

    ::v-deep .v-label {
        transform-origin: top left;
        transform: translateY(-10px) scale(0.75);
        position: absolute!important;
        left: 0;
    }
</style>