<template>
    <div class="relative-wrapper blue-grey lighten-3">
        <div class="absolute-wrapper">
            <VTabs
                    background-color="blue-grey"
                    dark
                    height="64"
                    v-model="tabs"
            >
                <VTab>
                    <VIcon>mdi-image</VIcon>
                    
                </VTab>
                <VTab>
                    <VIcon>mdi-layers</VIcon>
                    
                </VTab>
                <VTab :disabled="disableFilterTab">
                    <VIcon>mdi-image-filter-vintage</VIcon>
                    
                </VTab>
            </VTabs>

            <VTabsItems v-model="tabs" class="blue-grey lighten-3">
                <VTabItem>
                    <BackgroundProperties
                            @file-selected="$emit('bg-image-file-selected', $event)"
                            @url-selected="$emit('bg-image-url-selected', $event)"
                            @clear-background="$emit('clear-bg')"
                            :enable-drop-zone="tabs === 0"
                    />
                </VTabItem>

                <VTabItem>
                    <VExpandTransition>
                        <template v-if="selectedLayer">
                            <TextLayer
                                    v-if="selectedLayer.type === 'text'"
                                    :text.sync="selectedLayer.text"
                                    :font-size.sync="selectedLayer.fontSize"
                                    :color.sync="selectedLayer.color"
                                    :char-spacing.sync="selectedLayer.charSpacing"
                                    :font-family.sync="selectedLayer.fontFamily"
                                    :font-weight.sync="selectedLayer.fontWeight"
                                    :font-style.sync="selectedLayer.fontStyle"
                                    :underline.sync="selectedLayer.underline"
                                    :linethrough.sync="selectedLayer.linethrough"
                                    :overline.sync="selectedLayer.overline"
                                    :text-align.sync="selectedLayer.textAlign"
                                    :line-height.sync="selectedLayer.lineHeight"
                                    :shadow-color.sync="selectedLayer.shadowColor"
                                    :shadow-x-offset.sync="selectedLayer.shadowXOffset"
                                    :shadow-y-offset.sync="selectedLayer.shadowYOffset"
                                    :shadow-blur.sync="selectedLayer.shadowBlur"
                                    :stroke-color.sync="selectedLayer.stroke"
                                    :stroke-width.sync="selectedLayer.strokeWidth"
                                    :opacity.sync="selectedLayer.opacity"
                                    :background-color.sync="selectedLayer.textBackgroundColor"
                                    :name.sync="selectedLayer.name"
                                    :font-family-names="fontFamilyNames"
                                    @delete="removeLayer(selectedLayer)"
                            />

                            <ImageLayer
                                    v-else-if="selectedLayer.type === 'image'"
                                    :image="selectedLayer.image"
                                    :name.sync="selectedLayer.name"
                                    :opacity.sync="selectedLayer.opacity"
                                    :shadow-color.sync="selectedLayer.shadowColor"
                                    :shadow-x-offset.sync="selectedLayer.shadowXOffset"
                                    :shadow-y-offset.sync="selectedLayer.shadowYOffset"
                                    :shadow-blur.sync="selectedLayer.shadowBlur"
                                    :stroke-color.sync="selectedLayer.stroke"
                                    :stroke-width.sync="selectedLayer.strokeWidth"
                                    @delete="removeLayer(selectedLayer)"
                            />
                        </template>
                    </VExpandTransition>

                    <div class="pa-1">
                        <VRow dense>
                            <VCol cols="6">
                                <VCard>
                                    <FileSelectionArea @input="$emit('add-image-layer', $event)" :enable-drop-zone="tabs === 1">
                                        <template>
                                            <VResponsive
                                                    :aspect-ratio="1"
                                                    class="d-flex align-center justify-center blue-grey lighten-1"
                                            >
                                                <div class="text-center white--text">
                                                    <VIcon size="48" color="white">mdi-image</VIcon>
                                                    <br>
                                                    
                                                </div>
                                            </VResponsive>
                                        </template>
                                    </FileSelectionArea>
                                </VCard>
                            </VCol>

                            <VCol cols="6">
                                <VCard @click="$emit('add-text-layer')">
                                    <VResponsive
                                            :aspect-ratio="1"
                                            class="d-flex align-center justify-center blue-grey lighten-1"
                                    >
                                        <div class="text-center white--text">
                                            <VIcon size="48" color="white">mdi-format-text</VIcon>
                                            <br>
                                            
                                        </div>
                                    </VResponsive>
                                </VCard>
                            </VCol>
                        </VRow>
                    </div>
                </VTabItem>

                <VTabItem>
                    <FilterProperties
                            :filters="filters"
                            :selected-filter="filter"
                            :filter-preview-image="filterPreviewImage"
                            @selected="$emit('update:filter', $event)"
                    />
                </VTabItem>
            </VTabsItems>
        </div>
    </div>
</template>

<script>
  import FileSelectionArea from '../../file-selection-area';
  import TextLayer from './layers/text-layer';
  import ImageLayer from './layers/image-layer';
  import BackgroundProperties from './background-properties';
  import FilterProperties from './filter-properties/filter-properties';

  import {isEqual} from 'lodash';


  export default {
    name: 'input-panel',

    props: {
      layers: Array,
      fontFamilyNames: Array,
      filter: [String, null],
      filters: Object,
      selectedLayer: [Object, null],
      filterPreviewImage: [String, null],
      disableFilterTab: Boolean
    },

    data() {
      return {
        localLayers: [],
        tabs: 0
      };
    },

    components: {
      FileSelectionArea,
      TextLayer,
      ImageLayer,
      BackgroundProperties,
      FilterProperties
    },

    watch: {
      layers: {
        handler(newLayers) {
          if (!isEqual(newLayers, this.localLayers)) {
            this.localLayers = newLayers.slice();
          }
        },
        deep: true,
        immediate: true
      },

      localLayers: {
        handler(newLayers) {
          if (!isEqual(newLayers, this.layers)) {
            this.$emit('update:layers', newLayers.slice());
          }
        },
        deep: true,
        immediate: true
      },

      tabs(newTab, oldTab) {
        if (newTab !== oldTab) {
          if (newTab === 2) {
            this.$emit('enter-filters');
          } else if (oldTab === 2) {
            this.$emit('leave-filters');
          }
        }
      }
    },

    methods: {
      removeLayer(layer) {
        this.$emit('remove-layer', layer);
      }
    }
  }
</script>

<style scoped lang="scss">

    .relative-wrapper {
        position: relative;
        max-height: 100%;
        height: 100%;
        width: 100%;

        .absolute-wrapper {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow-y: auto;
        }
    }
</style>