export default {
  name: 'v-fabric-background-image',

  inject: ['promiseFabricCanvas'],

  props: {
    image: Image
  },

  render(h) {
    return this.$slots.default ? h('div', this.$slots.default) : null;
  },

  mounted() {
    this.setBackgroundImage();
  },

  beforeDestroy() {
    this.clearBackgroundImage();
  },

  watch: {
    image(newImage) {
      if (newImage) {
        this.setBackgroundImage(newImage);
      } else {
        this.clearBackgroundImage();
      }
    }
  },

  methods: {
    setBackgroundImage(image = this.image) {
      return this.promiseFabricCanvas().then(canvas => {
        canvas.setBackgroundImage(image.src, () => canvas.renderAll(), this.calculatePosition(canvas, image));
      });
    },

    clearBackgroundImage() {
      return this.promiseFabricCanvas().then(canvas => {
        canvas.setBackgroundImage(false, () => {
          if (canvas.lowerCanvasEl) {
            canvas.renderAll();
          }
        });
      });
    },

    calculatePosition(canvas, img) {
      const canvasScale = canvas.getZoom(),
            canvasWidth = canvas.getWidth() / canvasScale,
            canvasHeight = canvas.getHeight() / canvasScale,
            canvasRatio = canvasWidth / canvasHeight,
            imageRatio = img.width / img.height;
      let scale, top, left;

      if (canvasRatio >= imageRatio) {
        scale = canvasWidth / img.width;
        left = 0;
        top = -((img.height * scale) - canvasHeight) / 2;
      } else {
        scale = canvasHeight / img.height;
        top = 0;
        left = -((img.width * scale) - canvasWidth) / 2;
      }

      return {
        top, left,
        scaleX: scale,
        scaleY: scale,
        originX: 'left',
        originY: 'top'
      };
    }
  }
};